<template>
  <div>
    <cn-header id="home"></cn-header>

    <cn-slide>
      <div slot="slide-image">
        <slick ref="slick" :options="slickOptions">
          <img src="./assets/slides/slide-01.jpg" alt="">
          <img src="./assets/slides/slide-01.jpg" alt="">
          <img src="./assets/slides/slide-01.jpg" alt="">
        </slick>
      </div>
    </cn-slide>

    <cn-slide id="sobre" class="sobre-wrapper">
      <h1 slot="slide-title">O Caldinho do Nenem</h1>

      <div slot="slide-image">
        <slick ref="slick" :options="slickOptions">
          <img src="./assets/slides/slide-02.jpg" alt="">
          <img src="./assets/slides/slide-02.jpg" alt="">
          <img src="./assets/slides/slide-02.jpg" alt="">
        </slick>
      </div>

      <p class="container" slot="slide-content">
        O Caldinho do Nenen  o refgio perfeito para quem busca momentos de descontrao entre a famlia ou cercado de amigos, acompanhados sempre do melhor da gastronomia. A casa tem entre suas especialidades os famosos Caldinhos, eleitos pela revista Veja como o melhor da Cidade. Entre as diversas opes do cardpio esto os frutos do mar, preparados com ingredientes frescos, carnes, aves e timas opes para petiscos ou refeies. So mais de 20 anos servindo com prazer e atendendo com excelncia. <br>
        A estrutura do Caldinho do Nenen inclui o maior espao kids da Cidade, amplo estacionamento com manobrista e rea climatizada. Comprove a nossa qualidade, vem pro Caldinho do Nenen.
      </p>
    </cn-slide>

    <cn-slide id="cardapio" class="cardapio-wrapper">
      <div slot="slide-image">
        <slick ref="slick" :options="slickOptions">
          <img src="./assets/slides/slide-03.jpg" alt="">
          <img src="./assets/slides/slide-03.jpg" alt="">
          <img src="./assets/slides/slide-03.jpg" alt="">
          <img src="./assets/slides/slide-03.jpg" alt="">
        </slick>
      </div>
    </cn-slide>

    <cn-contact id="contato" class="contato-wrapper"></cn-contact>

    <cn-map></cn-map>

    <cn-footer></cn-footer>
  </div>
</template>

<script>
  // Components
  import cnHeader from './components/Header.vue';
  import cnSlide from './components/Slide.vue';
  import cnContact from './components/Contact.vue';
  import cnMap from './components/Map.vue';
  import cnFooter from './components/Footer.vue';

  // Libs
  import Slick from 'vue-slick';

  export default {
    name: 'AppPorto',
    components: {
      Slick,
      cnHeader,
      cnSlide,
      cnContact,
      cnMap,
      cnFooter
    },
    data() {
      return {
        slickOptions: {
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: true,
          speed: 300
        }
      }
    }
  }
</script>

<style lang="scss">
  .my-slide {
    height: 700px;

    &:first-child {
      background-color: red;
    }
  }
</style>


